  }
  
  @-webkit-keyframes tloader-progressing {
    0% {
      width: 0;
    }
    10% {
      width: 40%;
    }
    20% {
      width: 75%;
    }
    30% {
      width: 95%;
    }
  }
  
  @keyframes tloader-progressing {
    0% {
      width: 0;
    }
    10% {
      width: 40%;
    }
    20% {
      width: 75%;
    }
    30% {
      width: 95%;
    }
  }
  
  @-webkit-keyframes tloader-progressed {
