    }
  }
  
  @keyframes tloader-progressed {
    0% {
      opacity: 1;
    }
  }
  
  .tloader-progress {
    position: relative;
  }
  
  .tloader-progress:before {
    content: '';
    z-index: 1000;
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    /* background-color: #08bf06; */
    width: 99%;
    -webkit-animation: tloader-progressing 9s ease-out;
